import { Component, OnInit } from '@angular/core';
import { RouterLink } from '@angular/router';
import { 
  IonButton, 
  IonContent, 
  IonHeader, 
  IonTitle, 
  IonToolbar, 
  IonCard,
  IonCardContent,
  IonIcon,
  IonList,
  IonItem,
  IonLabel,
  IonGrid,
  IonRow,
  IonCol,
  IonButtons
} from '@ionic/angular/standalone';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { addIcons } from 'ionicons';
import { 
  medkitOutline, 
  calendarOutline, 
  alertOutline, 
  pulseOutline,
  notificationsOutline,
  timeOutline,
  exitOutline,
  checkmarkCircleOutline
} from 'ionicons/icons';
import { ElderlyService } from '../../services/elderly.service';

@Component({
  selector: 'app-home-idoso',
  templateUrl: './home-idoso.page.html',
  styleUrls: ['./home-idoso.page.scss'],
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    IonHeader, 
    IonToolbar, 
    IonTitle, 
    IonContent, 
    IonButton,
    IonCard,
    IonCardContent,
    IonIcon,
    IonList,
    IonItem,
    IonLabel,
    IonGrid,
    IonRow,
    IonCol,
    IonButtons,
    RouterLink
  ]
})
export class HomeIdosoPage implements OnInit {
  nome: string = '';
  userData: any = null;
  lembretes = [
    { titulo: 'Tomar remédio', horario: '08:00', concluido: false },
    { titulo: 'Consulta médica', horario: '14:30', concluido: false },
    { titulo: 'Ligação familiar', horario: '18:00', concluido: false }
  ];

  constructor(private elderlyService: ElderlyService) {
    addIcons({
      'medicine-outline': medkitOutline,
      'calendar-outline': calendarOutline,
      'alert-outline': alertOutline,
      'pulse-outline': pulseOutline,
      'notifications-outline': notificationsOutline,
      'time-outline': timeOutline,
      'exit-outline': exitOutline,
      'checkmark-circle-outline': checkmarkCircleOutline
    });
  }

  ngOnInit() {
    this.loadElderlyData();
  }

  loadElderlyData() {
    // Obter dados do usuário idoso do localStorage ou do token
    const elderlyToken = this.elderlyService.getElderlyToken();
    if (elderlyToken) {
      // Decodificar o token para obter os dados do usuário
      try {
        const tokenPayload = JSON.parse(atob(elderlyToken.split('.')[1]));
        console.log('Token payload:', tokenPayload);
        this.nome = tokenPayload.username || 'Idoso';
        this.userData = {
          id: tokenPayload.sub,
          name: tokenPayload.username,
          role: tokenPayload.role
        };
      } catch (error) {
        console.error('Erro ao decodificar token:', error);
        this.nome = 'Idoso';
      }
    } else {
      console.log('Token não encontrado');
      this.nome = 'Idoso';
    }
  }

  sair() {
    console.log('Saindo...');
    this.elderlyService.elderlyLogout();
    // Redirecionar para página de entrada ou login
    window.location.href = '/entrada';
  }
}
